body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #d1921c;
}

.bar {
  margin-bottom: 0 !important;
}

.bar {
  font-weight: bold;
}

.cert {
  height: 85vh;
  background: url(/APML-Certification/static/media/cert.df82856f.png) no-repeat;
  background-size: auto;
  margin-left: auto;
  margin-top: auto;
  margin-left: auto;
  margin-bottom: auto;
}

.pc {
  width: 200vh;
  height: auto;
  /* background: url("b3.jpg") no-repeat; */
  background: linear-gradient(to bottom, #cc66ff 0%, #3399ff 100%);
  background-size: auto;
}
#fname {
  font-size: 55px;
  padding-top: 315px;
  padding-left: 20px;
}

#course {
  font-size: 23px;
  margin-top: 3px;
  margin-left: 25px;
  font-weight: bold;
}

#txh {
  margin-top: 90px;
  margin-left: -300px;
}

#id {
  margin-top: -21px;
  margin-left: 490px;
}

.btn {
  margin-top: 10px;
}

